import { useCallback } from 'react';
import { useDispatch } from 'react-redux'
import { updatePosition } from '../reducers/locationSlice.js'
import useFetchDemoData from './useFetchDemoData.ts';
import useFetchTempAnn from './useFetchTempAnn.ts';
import useFindNearestPlace from './useFindNearestPlace.ts';

const useUpdatePositionAndData = () => {
    const dispatch = useDispatch()

    const {fetchDemoData} = useFetchDemoData();
    const {fetchTempAnn} = useFetchTempAnn();
    const {findNearestPlace} = useFindNearestPlace();

    const updatePositionAndData = useCallback((lat: number, lng: number, updatePlaceName: boolean, debug: string) => {
        console.log(`updatePositionAndData: Called by ${debug}`)

        dispatch(updatePosition({ lat: lat, lng: lng }));

        // fetch the data for this new position
        fetchDemoData(lat, lng, `updatePositionAndData+${debug}`)
        fetchTempAnn(lat, lng, `updatePositionAndData+${debug}`)

        if (updatePlaceName) {
            // If necessary, find the nearest place (name) for this position
            // (not required if the place name was chosen through the dropdown)
            findNearestPlace(lat, lng, `updatePositionAndData+${debug}`);
        }
    }, []);

    return { updatePositionAndData };
};

export default useUpdatePositionAndData;
