import { useRef, useCallback } from 'react';
import fetchZarrData from './fetchZarrData.ts'
import { useDispatch } from 'react-redux'
import { updateAverageTemp } from '../reducers/dataSlice.js'

const useFetchDemoData = () => {
    const abortControllerDemo = useRef<AbortController | null>(null);
    const dispatch = useDispatch()

    const fetchDemoData = useCallback((lat: number, lng: number, debug: string) => {
        console.log(`fetchDemoData: Called by ${debug}`)
        dispatch(updateAverageTemp("Loading..."));

        abortControllerDemo.current?.abort();
        abortControllerDemo.current = new AbortController();

        const query = `temperature-demo?x=${lng}&y=${lat}`;

        fetchZarrData(query, abortControllerDemo.current.signal)
            .then((result) => {
                const averageTempString = `${result.values.toFixed(1)}°C`;
                dispatch(updateAverageTemp(averageTempString));
                abortControllerDemo.current = null;
            })
            .catch(() => {
                dispatch(updateAverageTemp("Error loading data"));
            });
    }, []);

    return { fetchDemoData };
};

export default useFetchDemoData;
