import { createSlice } from '@reduxjs/toolkit'

export const dataSlice = createSlice({
    name: 'data',

    initialState: {
        averageTemp: "Loading...",
        tempAnn: {
            clim1: {
                time: [0, 1],
                values: [0, 1],
                clim_period: "Unknown",
            },
            clim2: {
                time: [0, 1],
                values: [0, 1],
                clim_period: "Unknown",
            },
        },
        tempAnnLoading: true,
    },
    
    reducers: {
        updateAverageTemp: (state, newAverageTemp) => {
            state.averageTemp = newAverageTemp.payload;
        },
        updateTempAnn: (state, newTempAnn) => {
            state.tempAnn = newTempAnn.payload;
            state.tempAnnLoading = false;
        },
        setTempAnnLoading: (state) => {
            state.tempAnnLoading = true;
        }
    }
})

// Action creators are generated for each case reducer function
export const { updateAverageTemp, updateTempAnn, setTempAnnLoading } = dataSlice.actions

export default dataSlice.reducer