import "leaflet-velocity/dist/leaflet-velocity.css";
import "leaflet-velocity/dist/leaflet-velocity.js";
import { useEffect } from "react";
import L from "leaflet";
import { useMap } from "react-leaflet";

const LeafletVelocity = () => {
    const map = useMap();

    useEffect(() => {
        if (!map) return;

        let mounted = true;
        let windGlobalLayer;

        // const colorScale = [
        //     "#00000044",
        //     "#00000077",
        //     "#000000aa",
        //     "#000000dd",
        //     "#000000ff"]

        // const colorScale = [
        //     "#ffffff44",
        //     "#ffffff77",
        //     "#ffffffaa",
        //     "#ffffffdd",
        //     "#ffffffff"];

        // const colorScale = ["#aaaaaa"]
        const colorScale = ["#ffffff"]

        console.log("VELOCITY");

        fetch("https://onaci.github.io/leaflet-velocity/wind-global.json")
            .then((response) => response.json())
            .then((data) => {
                if (!mounted) return;

                windGlobalLayer = L.velocityLayer({
                    displayValues: false,
                    displayOptions: {
                        velocityType: "GBR Water",
                        position: "bottomleft",
                        emptyString: "No water data"
                    },
                    data: data,
                    // minVelocity: 0.0,
                    maxVelocity: 15,
                    velocityScale: 0.007, // modifier for particle animations, arbitrarily defaults to 0.005
                    colorScale: colorScale,
                    // onAdd: null, // callback function
                    // onRemove: null, // callback function
                    opacity: 1, // layer opacity, default 0.97
                });

                console.log("VELOCITY - MOUNTED");

                if (map && windGlobalLayer) {
                    windGlobalLayer.addTo(map);
                }
            })
            .catch((err) => console.log("VELOCITY", err));

        return () => {
            mounted = false;
            if (map && windGlobalLayer) {
                map.removeLayer(windGlobalLayer);
            }
        };
    }, [map]);

    return null;
};

export default LeafletVelocity;