import { useRef, useCallback } from 'react';
import fetchZarrData from './fetchZarrData.ts'
import { useDispatch } from 'react-redux'
import { updateTempAnn, setTempAnnLoading } from '../reducers/dataSlice.js'

const useFetchTempAnn = () => {
    const abortControllerDemo = useRef<AbortController | null>(null);
    const dispatch = useDispatch()

    const fetchTempAnn = useCallback((lat: number, lng: number, debug: string) => {
        console.log(`fetchTempAnn: Called by ${debug}`)

        abortControllerDemo.current?.abort();
        abortControllerDemo.current = new AbortController();

        const query = `warming-stripes?x=${lng}&y=${lat}`;

        dispatch(setTempAnnLoading())

        fetchZarrData(query, abortControllerDemo.current.signal)
            .then((result) => {
                dispatch(updateTempAnn(result));
                abortControllerDemo.current = null;
            })
            .catch(() => {
                // dispatch(updateAverageTemp("Error loading data"));
            });
    }, []);

    return { fetchTempAnn };
};

export default useFetchTempAnn;
