import React, { useEffect, useState, useRef } from 'react';
import { Paper, Stack, ScrollArea, Badge} from '@mantine/core';
import scrollBarClass from './ScrollBar.module.css';
import { useSelector } from 'react-redux';
import MakeGraph from './MakeGraph.tsx';
import ScienceBox from './ScienceBox.tsx'

export default function WarmingStripes({ width, height, position, landscape }) {
    const data = useSelector(state => state.data.tempAnn)
    const isLoading = useSelector(state => state.data.tempAnnLoading)
    
    return (
        <ScienceBox width={width} height={height} position={position} variable={"Temperature warming stripes"} landscape={landscape} data={data} isLoading={isLoading} />
    )
};

